*[name="message"]{
    background-color: black;
    color: white;
    font-family: var(--p-font);
    display: block;
    margin: 10px auto;
    
}

*[name="message"]:hover{
    background-color: var(--pinkcolor);
    ;
}

form input{
    width: 350px;
    height: 30px;
}

textarea{
    width: 350px;
    padding: 5px;
}

.input-container{
    display: flex;
    justify-content: center;
    align-items: center;
    margin: 1rem;
    
    
}

#questions{
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
}

.submit-btn{
    background-color: var(--pinkcolor);
    width: 125px;
    height: 30px;
    border: none;
    color: white;
    font-size: 17px;
    align-self: flex-end;
    /* margin-left: 220px; */
    

   
    
}

.input{
    display: flex;
    flex-direction: column;
   
}
.submit-btn:hover{
    background-color: black;
    color: var(--pinkcolor);
}


.msg{
    visibility: hidden;
    background-color: rgba(112,175,24, 1);
    width: 250px;
    text-align: center;
    font-family: var(--p-font);
}

