.msgAppear{
    visibility: unset;
    font-weight: 600;
    margin: 5px;
    padding: 5px;
    background-color: rgba(112,175,24, 1);
    height: 30px;

    
}

.error{
    color:red;
    padding: 0px;
    margin-bottom: -10px;
    text-align: start;
    font-size: 12;
 
}

.error-msg{
    width: 250px;
    height: 30px;
    background-color: rgba(221, 55, 130, .5);
    text-align: center;
    padding: 5px;
    font-family: var(--p-font);
    margin: 5px;
    font-weight: 600;
}

::placeholder{
    color: white;
}
label{
    text-align: start;
}
/* span{
    font-size: 12px;
    color: red;
    padding:  3px;
    display: none;
} */

input:invalid[focused="true"] ~ span {
    display: block;
}


@media screen and (max-width: 380px) {
    
    form input{
        width: 250px;
        height: 30px;
    }

    textarea{
        width: 250px;
        padding: 5px;
    }
    
}


